@use "../variables";

.hero__content h2 {
  font-family: variables.$ff-roboto-condensed;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  color: variables.$color-alice-blue;
}

.hero__content p {
  font-family: variables.$ff-pt-mono;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  text-transform: lowercase;
  color: variables.$color-alice-blue;
}

.hero__link-btn {
  margin: 35px 0 45px;
  padding: 10px 15px;
  border: none;
  border-radius: 3px;
  font-family: variables.$ff-roboto-condensed;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  color: variables.$color-alice-blue;
  background-color: variables.$color-sky-blue;
  text-transform: uppercase;
}
